/**
 * Project specific styling.
 */

.site-view, footer, .project-header {
  display:none !important;
}

#wrapper {
    width:520px;
    margin-left:auto;
    margin-right:auto;
    font-family: "Benton Sans", Helvetica, Arial, sans-serif !important;
}

#chartTrend .c3-line {
    opacity:1 !important;
}

.chart-tooltip {
  font-size:0.8em !important;
}

#mapSearch {
  width:100%;
  height:500px;
}

.toprow {
  text-align:center;
  font-weight:bold;
  font-family: Courier New;
}

.divide {
  border-right:2px solid #ffffff;
}

#chartTime {
  font-size:0.6em;
  margin-top:10px;
  display:table;
  width:100%;
}

.row {
  display:table-row;
}

.row div {
  display:table-cell;
}

.type {
  text-align:left;
}

.mapboxgl-ctrl-logo, .mapboxgl-ctrl-bottom-right {
  display:none !important;
}

 .mapboxgl-canvas {
  position:static !important;
 }
 
 .mapboxgl-ctrl-compass {
display: none !important;
}

.breaker {
  margin:17px;
}

.label {
  font-weight:bold;
  font-size:0.9em;
}

.stat {
  float:right;
}

.chart .c3-line {
    stroke-width: 6px !important;
}

.c3-target-Overall .c3-line{
  stroke-dasharray: 5,5;
  stroke-width: 3px !important;
}

.c3-circles-Overall {
  display: none !important;
}

.smallchart {
  display:inline-block;
  width:32%;
  vertical-align:top;
}

.smallchart .chart {
    height:120px;
}

.smallchart .chart .c3-line {
    stroke-width:1px !important;
}

.bar div {
  display:inline-block;
  height:35px;
  color:#ffffff;
  font-size:0.7em;
  font-weight:bold;
  text-align:right;
  padding:2px;
}

    .chart-tooltip {
      background-color: rgba(0, 0, 0, 0.8);
      color: #FFFFFF;
      font-size: 0.85em;
      padding: 0.5em 1em;

      .tooltip-label {
        font-weight: 900;
        display: inline-block;
        padding-right: 0.5em;
      }
    }

.mainbar div {
  display:block !important;
}

#chartType {
  height:400px;
}

.byline a {
    color: #888 !important;
}

.noclicky {
  pointer-events:none !important;
}

.tooltip {
  width:180px !important;
}

.byline {
    line-height:140%;
    font-family: "Benton Sans", Helvetica, Arial, sans-serif;
    font-weight: 400;
    font-size:0.7em !important;
    color: #888;
    padding-top:10px;
    padding-bottom:10px;
    text-align:left;
}

.districtName {
  font-weight:bold;
}

.red5 {
  color:#ffffff;
}

.legendary {
    padding:4px;
    color: #fff;
    font-weight:bold;
    border-radius: 5px;
    padding: 0 7px;
    margin-bottom: 3px;
}

.chatter {
    line-height:140%;
}

.chartTitle {
	font-size:1.5em;
	font-weight:900;
  font-family: "Benton Sans", Helvetica, Arial, sans-serif;
}

.map {
	text-align:center;
}

@media (max-width:520px) {
    #wrapper {
        width:100%;
    }
    .smallchart {
      display:inline-block;
      width:49.5%;
    }
    .xwidth {
      width:100%;
    }
}

/*-- Chart --*/
.c3 svg {
  font: 10px sans-serif;
  -webkit-tap-highlight-color: transparent;
}

.c3 path, .c3 line {
  fill: none;
  stroke: #000;
}

.c3 text {
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
}

.c3-legend-item-tile, .c3-xgrid-focus, .c3-ygrid, .c3-event-rect, .c3-bars path {
  shape-rendering: crispEdges;
}

.c3-chart-arc path {
  stroke: #fff;
}

.c3-chart-arc text {
  fill: #fff;
  font-size: 13px;
}

/*-- Axis --*/
/*-- Grid --*/
.c3-grid line {
  stroke: #aaa;
}

.c3-grid text {
  fill: #aaa;
}

.c3-xgrid, .c3-ygrid {
  stroke-dasharray: 3 3;
}

/*-- Text on Chart --*/
.c3-text.c3-empty {
  fill: #808080;
  font-size: 2em;
}

/*-- Line --*/
.c3-line {
  stroke-width: 1px;
}

/*-- Point --*/
.c3-circle._expanded_ {
  stroke-width: 1px;
  stroke: white;
}

.c3-selected-circle {
  fill: white;
  stroke-width: 2px;
}

/*-- Bar --*/
.c3-bar {
  stroke-width: 0;
}

.c3-bar._expanded_ {
  fill-opacity: 0.75;
}

/*-- Focus --*/
.c3-target.c3-focused {
  opacity: 1;
}

.c3-target.c3-focused path.c3-line, .c3-target.c3-focused path.c3-step {
  stroke-width: 2px;
}

.c3-target.c3-defocused {
  opacity: 0.3 !important;
}

/*-- Region --*/
.c3-region {
  fill: steelblue;
  fill-opacity: 0.1;
}

/*-- Brush --*/
.c3-brush .extent {
  fill-opacity: 0.1;
}

/*-- Select - Drag --*/
/*-- Legend --*/
.c3-legend-item {
  font-size: 12px;
}

.c3-legend-item-hidden {
  opacity: 0.15;
}

.c3-legend-background {
  opacity: 0.75;
  fill: white;
  stroke: lightgray;
  stroke-width: 1;
}

/*-- Title --*/
.c3-title {
  font: 14px sans-serif;
}

/*-- Tooltip --*/
.c3-tooltip-container {
  z-index: 10;
}

.c3-tooltip {
  border-collapse: collapse;
  border-spacing: 0;
  background-color: #fff;
  empty-cells: show;
  -webkit-box-shadow: 7px 7px 12px -9px #777777;
  -moz-box-shadow: 7px 7px 12px -9px #777777;
  box-shadow: 7px 7px 12px -9px #777777;
  opacity: 0.9;
}
.c3-tooltip tr {
  border: 1px solid #CCC;
}
.c3-tooltip th {
  background-color: #aaa;
  font-size: 14px;
  padding: 2px 5px;
  text-align: left;
  color: #FFF;
}
.c3-tooltip td {
  font-size: 13px;
  padding: 3px 6px;
  background-color: #fff;
  border-left: 1px dotted #999;
}
.c3-tooltip td > span {
  display: inline-block;
  width: 10px;
  height: 10px;
  margin-right: 6px;
}
.c3-tooltip td.value {
  text-align: right;
}

/*-- Area --*/
.c3-area {
  stroke-width: 0;
  opacity: 0.2;
}

/*-- Arc --*/
.c3-chart-arcs-title {
  dominant-baseline: middle;
  font-size: 1.3em;
}

.c3-chart-arcs .c3-chart-arcs-background {
  fill: #e0e0e0;
  stroke: none;
}
.c3-chart-arcs .c3-chart-arcs-gauge-unit {
  fill: #000;
  font-size: 16px;
}
.c3-chart-arcs .c3-chart-arcs-gauge-max {
  fill: #777;
}
.c3-chart-arcs .c3-chart-arcs-gauge-min {
  fill: #777;
}

.c3-chart-arc .c3-gauge-value {
  fill: #000;
  /*  font-size: 28px !important;*/
}

